.finale {
  &-page {
    img {
      display: block;
      margin: 85px auto 25px;
    }

    &--fin {
      img {
        margin-top: 20px;
      }
    }
  }

  &-bar {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: 51px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #4d80b2;
    border-radius: 25px;
    width: 100%;
    max-width: 480px;
    margin: 0 auto 57px;

    &--fin {
      border-color: #df742c;
    }

    &-title {
      position: relative;
      z-index: 2;
      display: block;
      font-size: 30px;
      line-height: 34px;
      font-weight: bold;
      color: #fff;
      text-align: center;
    }

    &-progres {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      background-color: #df742c;
      border-radius: 25px;
      height: 100%;
    }
  }

  &-btns {
    width: 200px;

    a {
      width: 100%;
      min-height: 60px;
      padding: 5px;

      & + a {
        margin-top: 19px;
      }
    }
  }
}
